nav {
    padding: 1rem 0rem;
    border-bottom: 1px solid #dddddd3b;
    position: fixed;
    top: 0%;
    left: 0;
    right: 0;
    width: 100%;
    font-size: 17px;
    z-index: 100;
    @include mainTransition;
    &.show {
        background: rgba(0, 0, 0, 0.8);
        backdrop-filter: blur(3px);
        border-bottom: none;
        padding: .5rem 0rem; }
    .row {
        align-items: center;
        @media (max-width:580px) {
            .col-sm-6 {
                max-width: 50%; } } }
    .logo {
        width: 6rem;
        @media (max-width:350px) {
            width: auto; }
        img {
            width: 100%;
            @media (max-width:350px) {
                width: 60%; } } }
    .content {
        display: flex;
        justify-content: right;
        a,button {
            color: $main_color;
            display: flex;
            align-items: center;
            padding: 5px 10px;
            position: relative;
            cursor: pointer;
            @media (max-width:350px) {
                font-size: 10px; }
            &::after {
                content: '';
                position: absolute;
                height: 1px;
                width: 100%;
                background: $main_color;
                bottom: 0px;
                left: 0;
                transform: scale(0);
                transform-origin: left;
                @include mainTransition; }
            img {
                margin-right: 5px;
                @media (max-width:350px) {
                    width: 15px; } }

            &:hover {
                &::after {
                    transform: scale(1); } } }
        button {
            background: transparent;
            border: none;
            outline: none; }
        a {
            &::after {
                transform-origin: right !important; } } } }
